	transform: scale(1.0);
	margin-right: 1vw;
	transition: 0.2s;
}

.activesidenavtabs{
	height: 4vw;
}
.activesidenavtabs a::before{
	content:'';
	width: 2vw;
	height: 2vw;
	background-color:rgb(15,18,89);
	border-bottom-left-radius: 1vw;
	box-shadow: -10px 10px 0px 0px white;
	position: absolute;
	left:0;
	top:-2vw;
}

.activesidenavtabs a::after{
	content:'';
	width: 2vw;
	height: 2vw;
	background-color:rgb(15,18,89);
	border-top-left-radius: 1vw;
	box-shadow: -10px -10px 0px 0px white;
	position: absolute;
	left:0;
	bottom:-2vw;
}



.activesidenavtabs a{
	color:black;
	border-radius: 0px 20px 20px 0px;
	background-color: white;
	padding-top: 0.5vw;
	padding-bottom: 0.5vw;
